@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap');

*{
  padding:0; margin:0;
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
}
a{
  text-decoration: none;
  color:#fff;
}
a:visited{
  color:#fff;
}

.grap{
  background:#fff;
  padding:30px 50px;
  margin:0 auto;
  height:700px;
  width:80%;
  border:1px solid #000;
  border-top:none;
}
.grap svg text{
  fill:#333 !important;
}
section{
  text-align: center;
  margin:0px auto;
  width:80%;
}
table{
  margin-top:30px;
  width:100%;
  border-collapse: collapse;
  border:1px solid #000;
}
th,td{
  padding:10px;
}
th{
  background-color: #ddd;
}
td{
  background:#fff;
}


.current_weather{
  width:fit-content;
  position: absolute;
  left:50%; bottom:30px;
  transform: translateX(-50%);
  background:rgba(0,0,0,0.5);
  border-radius: 15px;
  padding:10px;
  width:80%;
  text-align: center;
  color:#fff;
  background:url('img/bg-2.jpeg') center no-repeat;
  background-size: cover;
  color:#000;
}
.current_weather p{
  font-size:14px;
  margin-bottom:10px;
}
.city{
  font-size:24px;
  font-style: italic;
  font-family: 'Do Hyeon', sans-serif;
}
.weather{
  font-size:18px;
  line-height: 1.5;
}
.temp{
  font-size:18px;
  line-height: 1.5;
}

li{
  list-style: none;
}
.header{
  z-index:10;
  position:fixed;
  left:0;top:0;
  box-shadow: 10px 0 20px rgba(0,0,0,0.5);
  width:20vw; height:100vh;
  text-align: center;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background:#222;
}
.header .logo{
  background:url('img/bg-3.jpeg') center no-repeat;
  background-size: cover;
  font-family: 'Do Hyeon', sans-serif;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width:100%;
  height:200px;
  font-weight: 700;
  font-size:40px;
  color:#fff;
}
.subtit{
  font-size:18px;
}
.header ul{
  width:100%;
}
.header ul li{
  cursor: pointer;
  font-size:12px;
  color:#fff;
  display:flex;
  justify-content: flex-end;
  align-items: center;
  padding-right:20px;
  width:100%;
  height:50px;
  background:linear-gradient(to bottom,#444,#333);
}
.header ul li.active{
  font-weight: 900;
  background:linear-gradient(to bottom,rgb(241, 228, 186),rgb(180, 112, 112));
}
main{
  width:80vw;
  margin:0 0 0 20vw;
}
main ul{
  width:100%;
  display:flex;
  flex-wrap: wrap;
}
main ul li{
  position: relative;
  width:33.32%;height:250px;
  background:#eee;
  overflow: hidden;
}
main ul li img{
  width:150%;
  position: absolute;
  left:50%;top:50%;
  transform:translate(-50%,-50%);
}
main ul li:hover .hover{
  display:flex;
}
.hover{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position:absolute;
  left:0;top:0;
  width:100%;height:100%;
  background:rgba(0,0,0,0.6);
  color:#fff;
  display:none;
}
.circle{
  width:50px; height:50px;
  border:1px solid #fff;
  border-radius: 30px;
  display:flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
}
.circle:hover{
  background:rgba(255, 255, 255, 0.6);
  border:1px solid rgba(255, 255, 255, 0.6);
  color:#000;
  font-weight: 900;
}
.line{
  width:30%;
  margin:20px;
  height:1px;
  background-color: #fff;
}
.popup{
  z-index:100;
  position: fixed;
  left:0; top:0;
  width:100vw;
  height:100vh;
  overflow: hidden;
  background: rgba(0,0,0,0.8);
  display:none;
}
.popup.active{
  display:flex;
}
.popup img{
  position: absolute;
  left:50%;top:50%;
  transform: translate(-50%,-50%);
  height:90%;
  border-radius: 30px;
  box-shadow: 0 0 30px rgba(0,0,0,0.95);
}

.maps{
  margin-top:200px;
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
